import { createSlice } from "@reduxjs/toolkit";

let initialState ={
  count: 0,
  cartItems: [], //    
  productToDelete: null, //     
}

let counterSlice = createSlice({
  name: "counter",
  initialState,
  reducers: {
    hyzwed: (state) => {
      state.count += 1;
    },
    addToCart: (state, action) => {
      const product = action.payload; //      
      // action.payload        
      // console.log(product)
      state.cartItems.push(product);
    },
    removeFromCart: (state, action) => {
      const indexToRemove = action.payload; //   payload 
    //     
      state.cartItems.splice(indexToRemove, 1); //     
    },
    updateCartItemCount: (state) => {
      state.count = state.cartItems.length;
    },

  },
});

export let counterReducer = counterSlice.reducer;
export const { hyzwed, addToCart  ,removeFromCart , updateCartItemCount} = counterSlice.actions;
export const setProductToDelete = (state, action) => {
  state.productToDelete = action.payload; //      
};